const sketch = require('sketch')

function uniqueId() {
  return Math.random().toString(32).substr(2, 8);
};

let outImagePath = '/Users/picasso/project/shapeImage/1';

// 判断组中的所有图形是不是 ShapePath 的类型
const shapeGroup = (groupLayer) => {
  let count = 0;
  let len = 0;
  groupLayer.layers.map(item => {
    len += 1;
    if (item.type == 'ShapePath' || item.type == 'Shape') {
      count += 1;
    }
  })
  return count == len;
}

const renameGroup = (layer, sketchPath) => {
  layer.layers.map(item => {
    let exported = false;
    let path = (sketchPath.split('/')[sketchPath.split('/').length - 1]).split('.')[0].split('_')[1];
    if (item.type == 'ShapePath' || item.type == 'Shape') {
      sketch.export(item, {
        output: outImagePath,
        'use-id-for-name': true,
        scales: '2'
      })
    }

    if (item.layers && !exported) {
      renameGroup(item, sketchPath)
    }
  })
}

const runner = () => {
  let data = {
    "sketchPath": "/Users/picasso/project/easyui_server/input/fangcan_100.sketch",
    "artboardsIdList": ["543A92A0-6C7C-47C0-8859-324B3230BE16"]
  }
  let {
    sketchPath,
    artboardsIdList
  } = data;
  var Document = require('sketch/dom').Document;
  Document.open(sketchPath, (err, document) => {
    if (err) {
      // oh no, we failed to open the document
      log(err)
    } else {
      try {
        artboardsIdList.map(item => {
          let art = document.getLayerWithID(item);
          renameGroup(art, sketchPath)
        })
        document.close();
      } catch (error) {
        log(error)
      }
    }
  })
}

export default function (context) {
  runner()
}